export default {
	data() {
		return {
			currentNum:1,
			stu:null,
			dataList:null,
			pageType:null
		}
	},
	methods: {
		next()
		{
			this.currentNum++;
				if(this.pageType == 0){
					this.validReturnLog()
				}else if(this.pageType == 1){
					this.invalidReturnLog()
				}	
		},
		prevent()
		{
			this.currentNum--;
			if(this.pageType == 0){
				this.validReturnLog()
			}else if(this.pageType == 1){
				this.invalidReturnLog()
			}
			
		},
		
		// 有效回访日志
		validReturnLog()
		{
				console.log("我进来了吗")
		    uni.request({
		    	url:this.serverUrl + '/stu/visit_info/valid/'  + this.currentNum + '?name=&limit=1',
				method:'GET',
				data:{},
		
				success: visitDate =>{
					console.log(visitDate)
					this.stu = visitDate.data[0]
					console.log("复制成功了吗")
				},
				fail() {},
				complete() {},
		    });
		},	
		// 无效回访日志
		invalidReturnLog()
		{
			console.log(this.currentNum)
		    uni.request({
		    	url:this.serverUrl + '/stu/visit_info/invalid/'  + this.currentNum + '?limit=1',	
				method:'GET',
				data:{},
				success: invalidVisitDate =>{
					console.log(invalidVisitDate)
					this.stu = invalidVisitDate.data[0]
					console.log("stu:"+this.stu)
				},
				fail() {},
				complete() {},
		    });
		},	
	},
	computed:{
		hasPrevent:function(){
			return{
				'bg-red': true
			}
		}
	},
	onShow(param) {
		var me = this;
	},
	onLoad(param)
	{
		console.log("load... type:" + param.type)
		this.pageType = param.type;
		if(this.pageType == 0){
			this.validReturnLog();
		}else if(this.pageType == 1){
			this.invalidReturnLog();
		}
	}
	
}
